@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@100;300;400;500;600;700&display=swap');

* {
    font-family: 'Raleway', sans-serif;
}

body {

    box-sizing: border-box;
    margin: 0px;
    padding: 0px;


}

h1 {
    font-size: 50px;
}

h2 {
    font-size: 48px;
}

p {
    font-size: 20px;
}

.semi-bold{
    font-weight: 600;
}

.bold {
    font-weight: 700;
}

.overlay{
    width: 100%;
    height: 100%;
    background-color: aqua;
}


.contenedor {
    display: flex;
    width: 90%;
    gap: 20px;
    align-items: flex-start;
    justify-content: flex-start;
    margin: 75px auto;
}

.seleccionado {
    font-weight: 700;
    border-bottom:  2px solid black;
}

.pointer {
    cursor: pointer;
}

.textoHover {
    padding: 2px 10px;
}

.textoHover:hover {
    background-color: rgb(244, 246, 246);
    border-radius: 3px;
  
}

.puntos {
    width: auto;
    height: 20px;
}

body::-webkit-scrollbar {
    color: transparent;
    background-color: transparent;
    width: 7px;
}

body::-webkit-scrollbar-thumb {
    background-color: rgb(6, 45, 79);

}