@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@100;300;400;500;600;700&display=swap");
* {
  font-family: "Raleway", sans-serif;
}

body {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}

h1 {
  font-size: 50px;
}

h2 {
  font-size: 48px;
}

p {
  font-size: 20px;
}

.semi-bold {
  font-weight: 600;
}

.bold {
  font-weight: 700;
}

.overlay {
  width: 100%;
  height: 100%;
  background-color: aqua;
}

.contenedor {
  display: flex;
  width: 90%;
  gap: 20px;
  align-items: flex-start;
  justify-content: flex-start;
  margin: 75px auto;
}

.seleccionado {
  font-weight: 700;
  border-bottom: 2px solid black;
}

.pointer {
  cursor: pointer;
}

.textoHover {
  padding: 2px 10px;
}

.textoHover:hover {
  background-color: rgb(244, 246, 246);
  border-radius: 3px;
}

.puntos {
  width: auto;
  height: 20px;
}

body::-webkit-scrollbar {
  color: transparent;
  background-color: transparent;
  width: 7px;
}

body::-webkit-scrollbar-thumb {
  background-color: rgb(6, 45, 79);
}

header .imagen {
  position: relative;
  width: 100%;
  height: 200px;
  background-image: url("../../../build/img/header.webp");
  background-size: cover;
  background-repeat: no-repeat;
  margin-bottom: 50px;
}
header .header {
  width: 90%;
  margin: auto;
  padding: 30px 0px;
}
header .header h1 {
  background-color: aliceblue;
  padding: 30px 50px 30px 0px;
  border-radius: 10px;
  width: 45%;
  text-align: center;
  font-weight: 500;
}

.week {
  width: 85%;
  margin: auto;
}
.week .ajustes {
  height: 185px;
}
.week .ajustes .headerAjustes {
  display: flex;
  align-items: center;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 20px;
}
.week .ajustes .headerAjustes img {
  width: 45px;
}
.week .bodyAjustes {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.week .bodyAjustes .ajuste {
  display: flex;
  align-items: center;
  justify-content: center;
}
.week .bodyAjustes .ajuste img {
  width: 20px;
}
.week .bodyAjustes > div {
  position: relative;
}
.week .bodyAjustes button {
  border-radius: 10px;
  border: none;
  padding: 0px 10px;
  font-size: 19px;
  margin-left: 5px;
  height: 30px;
  font-weight: 500;
  letter-spacing: 1px;
}
.week .bodyAjustes button:hover {
  background-color: rgb(111, 124, 135);
  color: white;
  cursor: pointer;
}
.week .bodyAjustes button:active {
  background-color: white;
  color: black;
}
.week .bodyAjustes .lupa {
  display: flex;
  gap: 3px;
}
.week .bodyAjustes .lupa img {
  width: 25px;
}
.week .bodyAjustes .lupa > input {
  padding: 6px;
  border-radius: 5px;
}
.week .bodyAjustes .ocultarGrupos {
  position: absolute;
  width: 0px;
  height: 0px;
}
.week .bodyAjustes .ocultarGrupos p {
  font-size: 0px;
  margin: 0px;
  padding: 0px;
}
.week .bodyAjustes .ocultarGrupos img {
  width: 0px;
  height: 0px;
}
.week .bodyAjustes .mostrarGrupos {
  background-color: white;
  border-radius: 5px;
  color: black;
  display: flex;
  flex-direction: column;
  gap: 20px;
  height: auto;
  left: 279px;
  padding: 12px 10px 10px;
  position: absolute;
  top: 63px;
  box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.75);
  width: 200px;
}
.week .bodyAjustes .mostrarGrupos p {
  font-size: 20px;
}
.week .bodyAjustes .mostrarGrupos .agregarGrupos {
  border-radius: 5px;
  color: black;
  font-weight: 500;
  padding: 10px 5px;
}
.week .bodyAjustes .mostrarGrupos .agregarGrupos:hover {
  background-color: rgba(240, 240, 240, 0.95);
  cursor: pointer;
}
.week .bodyAjustes .mostrarGrupos .imagenesGrupo {
  padding: 0px;
  margin: 0px;
  width: 0px;
  height: 0px;
}
.week .bodyAjustes .mostrarGrupos .infoGrupo {
  height: 20px;
}
.week .bodyAjustes .mostrarGrupos .infoGrupo .bold {
  padding-top: 7px;
}
.week .bodyAjustes .mostrarGrupos .infoGrupo p {
  padding: 0px;
  font-size: 18px;
}
.week .bodyAjustes .mostrarGrupos .infoGrupo img {
  width: 0px;
}
.week .bodyAjustes .mostrarGrupos .infoGrupo:hover .imagenesGrupo {
  position: relative;
  top: -27px;
  left: 124px;
  width: 71px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  padding: 3px;
}
.week .bodyAjustes .mostrarGrupos .infoGrupo:hover img {
  padding: 3px;
  border-radius: 3px;
  width: 20px;
}
.week .bodyAjustes .mostrarGrupos .infoGrupo:hover img:hover {
  background-color: rgba(240, 240, 240, 0.95);
  cursor: pointer;
}
.week .bodyAjustes .mostrarGrupos .linea {
  border-bottom: 1px solid;
}
.week .bodyAjustes .mostrarGrupos p {
  padding: 0px;
  margin: 3px;
}
.week .bodyAjustes .mostrarGrupos .subgrupos {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 200px;
  height: 20px;
}
.week .bodyAjustes .mostrarGrupos .subgrupos p {
  padding: 3px;
  border-radius: 5px;
  margin-left: 7px;
  background-color: #dcc9fa;
  width: fit-content;
}
.week .bodyAjustes .mostrarGrupos .subgrupos .imagenesGrupo {
  padding: 0px;
  margin: 0px;
  width: 0px;
  height: 0px;
}
.week .bodyAjustes .mostrarGrupos .subgrupos .imagenesGrupo img {
  width: 0px;
  height: 0px;
}
.week .bodyAjustes .mostrarGrupos .subgrupos:hover .imagenesGrupo {
  position: relative;
  top: -31px;
  left: 145px;
  width: 47px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  padding: 3px;
}
.week .bodyAjustes .mostrarGrupos .subgrupos:hover .imagenesGrupo img {
  width: 20px;
  height: 20px;
  padding: 3px;
  border-radius: 3px;
  width: 20px;
}
.week .bodyAjustes .mostrarGrupos .subgrupos:hover .imagenesGrupo img:hover {
  background-color: rgba(240, 240, 240, 0.95);
  cursor: pointer;
}
.week .bodyAjustes .mostrarGrupos .subgrupos .nombreEditarSubgrupo {
  margin-left: 7px;
  padding: 3px;
  border-radius: 5px;
  margin-top: 5px;
  font-size: 15px;
  max-width: 120px;
}
.week .bodyAjustes > div {
  display: flex;
  gap: 20px;
  align-items: center;
}
.week .dias {
  padding-bottom: 20px;
  margin-bottom: 100px;
  overflow: auto;
}
.week .dias::-webkit-scrollbar {
  background-color: rgba(150, 166, 179, 0.1);
  height: 7px;
}
.week .dias::-webkit-scrollbar-thumb {
  background-color: rgb(182, 192, 201);
  padding: 200px;
}
.week .dias::-webkit-scrollbar-thumb:hover {
  background-color: rgb(6, 45, 79);
}
.week .dias > div {
  display: flex;
  justify-content: center;
  align-items: stretch;
  padding: 0px 10px;
  gap: 15px;
  width: 2500px;
  margin-top: 20px;
  border-top: 1px solid rgb(190, 190, 190);
}
.week .dias > div .dia {
  width: 320px;
  min-height: 450px;
  padding: 20px 10px;
  border-radius: 3px;
  margin: 20px 0px;
}
.week .dias > div .dia .headerDia {
  display: flex;
  justify-content: space-between;
  padding: 5px;
  font-weight: 400;
  letter-spacing: 1px;
}
.week .dias > div .dia .headerDia > div {
  display: flex;
  align-items: center;
  gap: 10px;
}
.week .dias > div .bodyDia p,
.week .dias > div .headerDia p {
  padding: 2px 12px;
  margin-bottom: 10px;
  margin: 0px;
}
.week .dias > div .bodyDia #contadorDia,
.week .dias > div .headerDia #contadorDia {
  font-weight: 200;
  font-size: 21px;
  padding: 2px 0px;
}
.week .dias > div .bodyDia .agregarTareas {
  font-weight: 400;
  padding: 10px 5px;
  border-radius: 5px;
  margin: 10px 0px 0px 0px;
}
.week .dias > div .bodyDia .agregarTareas:hover {
  background-color: rgba(203, 208, 211, 0.2);
  cursor: pointer;
}
.week .dias > div .bodyDia .agregarTareas p {
  padding: 0px;
}
.week .dias > div .formularioTarea {
  position: relative;
  margin-bottom: 20px;
  position: relative;
  width: 100%;
  margin: 10px auto;
  box-shadow: 0px 0px 3px 0px rgba(108, 107, 107, 0.75);
  border-radius: 5px;
}
.week .dias > div .formularioTarea form,
.week .dias > div .formularioTarea .select,
.week .dias > div .formularioTarea .subject {
  border-radius: 5px;
  background-color: white;
}
.week .dias > div .formularioTarea .select.subject {
  box-shadow: 0px 0px 3px 0px rgba(108, 107, 107, 0.75);
}
.week .dias > div .formularioTarea .select p {
  color: rgb(91, 91, 91);
  font-size: 18px;
  padding: 10px;
  width: auto;
}
.week .dias > div .formularioTarea .select p:hover {
  background-color: rgb(118, 137, 152);
  color: white;
  cursor: pointer;
}
.week .dias > div .formularioTarea .subjectOculto {
  height: 0px;
  width: 0px;
  font-size: 0px;
}
.week .dias > div .formularioTarea .subjectOculto p {
  height: 0px;
  font-size: 0px;
  padding: 0px;
}
.week .dias > div .formularioTarea .subject {
  max-height: 250px;
  position: absolute;
  width: 100%;
  overflow-y: auto;
  z-index: 1;
}
.week .dias > div .formularioTarea .subject p {
  height: auto;
  font-size: 17px;
}
.week .dias > div .formularioTarea .subject p:hover {
  background-color: rgb(146, 167, 185);
  border-radius: 0px;
  color: white;
  cursor: pointer;
}
.week .dias > div .formularioTarea .gurpos {
  max-height: 250px;
  width: 100%;
  overflow-y: auto;
}
.week .dias > div .formularioTarea .select:not(.subjectOculto) .grupos p {
  font-size: 17px;
  padding-left: 15px;
}
.week .dias > div .formularioTarea .grupos p:hover {
  background-color: rgb(168, 190, 213);
  color: white;
  cursor: pointer;
}
.week .dias > div .formularioTarea .grupos p::before {
  content: ": : ";
}
.week .dias > div .formularioTarea .gruposOcultos {
  height: 0px;
  width: 0px;
}
.week .dias > div .formularioTarea .gruposOcultos p {
  font-size: 0px;
  padding: 0px;
}
.week .dias > div .formularioTarea form input {
  border: 0px;
  background-color: transparent;
  padding: 13px;
  font-size: 18px;
}
.week .dias > div .formularioTarea form input:focus {
  outline: none;
  border: 0px solid white;
}
.week .dias > div .mostrarTarea {
  display: flex;
  flex-direction: column;
}
.week .dias > div .mostrarTarea .headerTarea {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: white;
  color: black;
  padding: 15px 10px;
  border-radius: 5px;
  margin-bottom: 10px;
  box-shadow: 0px 0px 3px 0px rgba(108, 107, 107, 0.75);
}
.week .dias > div .mostrarTarea .headerTarea .tarea {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  width: 100%;
  gap: 10px;
  z-index: 0;
}
.week .dias > div .mostrarTarea .headerTarea .tarea .tareaNombre {
  width: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}
.week .dias > div .mostrarTarea .headerTarea .tarea .tareaNombre .tareaAjustes {
  display: flex;
  position: absolute;
  left: 215px;
  top: 7px;
}
.week .dias > div .mostrarTarea .headerTarea .tarea .tareaNombre .tareaAjustes img {
  width: 0px;
}
.week .dias > div .mostrarTarea .headerTarea .tarea .tareaNombre > p {
  font-size: 18px;
  font-weight: 600;
  line-height: 1.3;
}
.week .dias > div .mostrarTarea .headerTarea .tarea .tareaGrupo > p {
  font-size: 16px;
  font-weight: 300;
  background-color: #dcc9fa;
  padding: 3px 6px;
  border-radius: 3px;
}
.week .dias > div .mostrarTarea .headerTarea .tarea p {
  padding: 0px;
}
.week .dias > div .mostrarTarea .headerTarea:hover .tarea .tareaNombre .tareaAjustes img {
  width: 20px;
  z-index: 1;
  padding: 5px 7px 5px 7px;
}
.week .dias > div .mostrarTarea .headerTarea:hover .tarea .tareaNombre .tareaAjustes {
  background-color: rgb(255, 255, 255);
  border-radius: 5px;
}
.week .dias > div .mostrarTarea .headerTarea:hover .tarea .tareaNombre .tareaAjustes img:hover {
  background-color: rgba(240, 240, 240, 0.95);
  border-radius: 5px;
  cursor: pointer;
}
.week .tabla {
  overflow: hidden;
}
.week .tabla.dias > div {
  flex-wrap: wrap;
  width: auto;
}
.week .tabla.dias > div .dia {
  flex: 1 1 320px;
  max-width: 320px;
}

.lunes {
  color: black;
  background-color: rgb(249, 249, 245);
}
.lunes .headerDia > div > p:first-child,
.lunes .agregarTareas:hover {
  color: rgb(50, 48, 44);
  background: rgb(227, 226, 224);
}
.lunes .agregarTareas {
  color: rgb(145, 145, 142);
}

.martes {
  color: black;
  background-color: rgb(250, 247, 237);
}
.martes .headerDia > div > p:first-child,
.martes .agregarTareas:hover {
  color: rgb(64, 44, 27);
  background: rgb(253, 236, 200);
}
.martes .agregarTareas {
  color: rgb(146, 143, 133);
}

.miercoles {
  color: black;
  background-color: rgb(252, 245, 242);
}
.miercoles .headerDia > div > p:first-child,
.miercoles .agregarTareas:hover {
  color: rgb(73, 41, 14);
  background: rgb(250, 222, 201);
}
.miercoles .agregarTareas {
  color: rgb(148, 141, 124);
}

.jueves {
  color: black;
  background-color: rgb(244, 248, 243);
}
.jueves .headerDia > div > p:first-child,
.jueves .agregarTareas:hover {
  color: rgb(28, 56, 41);
  background: rgb(219, 237, 219);
}
.jueves .agregarTareas {
  color: rgb(148, 141, 124);
}

.viernes {
  color: black;
  background-color: rgb(241, 248, 251);
}
.viernes .headerDia > div > p:first-child,
.viernes .agregarTareas:hover {
  color: rgb(24, 51, 71);
  background: rgb(211, 229, 239);
}
.viernes .agregarTareas {
  color: rgb(145, 141, 125);
}

.sabado {
  color: black;
  background-color: rgb(249, 246, 252);
}
.sabado .headerDia > div > p:first-child,
.sabado .agregarTareas:hover {
  background: rgb(232, 222, 238);
  color: rgb(65, 36, 84);
}
.sabado .agregarTareas {
  color: rgb(145, 144, 126);
}

.domingo {
  color: black;
  background-color: rgb(249, 249, 245);
}
.domingo .headerDia > div > p:first-child,
.domingo .agregarTareas:hover {
  color: rgb(50, 48, 44);
  background: rgb(227, 226, 224);
}
.domingo .agregarTareas {
  color: rgb(145, 145, 119);
}

.lista {
  min-height: 300px;
  width: 45%;
  margin: auto;
  background-color: aliceblue;
  padding: 30px;
  border-radius: 10px;
}
.lista h2 {
  margin: 0px;
}

.monkMode {
  min-height: 300px;
  width: 55%;
  margin: auto;
  display: flex;
  flex-direction: column;
  background-color: aliceblue;
  padding: 30px;
  border-radius: 10px;
}
.monkMode .header {
  height: 30px;
  display: flex;
  align-items: center;
  justify-items: center;
  gap: 30px;
}
.monkMode .dias {
  display: flex;
  gap: 30px;
}
.monkMode .ajustes {
  display: flex;
  gap: 30px;
}