.week {
    width: 85%;
    margin: auto;

    .ajustes {
        height: 185px;

        .headerAjustes {
            display: flex;
            align-items: center;
            flex-direction: row;
            align-items: center;
            justify-content: center;

            gap: 20px;

            img {
                width: 45px;
            }
        }

    }


    .bodyAjustes {

        .ajuste {
            display: flex;
            align-items: center;
            justify-content: center;

            img {
                width: 20px;
            }
        }

        &>div {
            position: relative;

        }

        width: 100%;

        display: flex;
        align-items: center;
        justify-content: space-between;

        button {
            border-radius: 10px;
            border: none;
            padding: 0px 10px;
            font-size: 19px;
            margin-left: 5px;
            height: 30px;
            font-weight: 500;
            letter-spacing: 1px;
        }

        button:hover {
            background-color: rgb(111, 124, 135);
            color: white;
            cursor: pointer;
        }

        button:active {
            background-color: white;
            color: black;
        }

        .lupa {
            display: flex;
            gap: 3px;

            img {
                width: 25px;
            }

            &>input {
                padding: 6px;
                border-radius: 5px;
            }

        }

        .ocultarGrupos {
            position: absolute;
            width: 0px;
            height: 0px;

            p {
                font-size: 0px;
                margin: 0px;
                padding: 0px;
            }

            img {
                width: 0px;
                height: 0px;
            }
        }

        .mostrarGrupos {
            p {
                font-size: 20px;
            }


            background-color: white;
            border-radius: 5px;
            color: black;
            display: flex;
            flex-direction: column;
            gap: 20px;
            height: auto;
            left: 279px;
            padding: 12px 10px 10px;
            position: absolute;
            top: 63px;
            box-shadow: 0px 0px 7px 0px rgb(0 0 0 / 75%);
            width: 200px;

            .agregarGrupos {
                border-radius: 5px;
                color: black;
                font-weight: 500;
                padding: 10px 5px;
            }

            .agregarGrupos:hover {
                background-color: rgba($color: rgb(240, 240, 240), $alpha: .95);
                cursor: pointer;
            }

            .imagenesGrupo {
                padding: 0px;
                margin: 0px;
                width: 0px;
                height: 0px;
            }

            .infoGrupo {
                height: 20px;

                .bold{
                    padding-top: 7px;
                }
            }

            .infoGrupo p {
                padding: 0px;
                font-size: 18px;
            }

            .infoGrupo img {
                width: 0px;
            }

            .infoGrupo:hover .imagenesGrupo {
                position: relative;
                top: -27px;
                left: 124px;
                width: 71px;
                height: 20px;
               
                display: flex;
                align-items: center;
                justify-content: center;
                border-radius: 5px;
                padding: 3px;
            }

            .infoGrupo:hover img {
                padding: 3px;
                border-radius: 3px;
                width: 20px;
               
            }

            .infoGrupo:hover img:hover {
                background-color: rgba($color: rgb(240, 240, 240), $alpha: .95);
                cursor: pointer;
            }


            .linea {
                border-bottom: 1px solid;
            }

            p {
                padding: 0px;
                margin: 3px;
            }

            .subgrupos {
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                width: 200px;
                height: 20px;

                p {
                    padding: 3px;
                    border-radius: 5px;
                    margin-left: 7px;
                    background-color: #dcc9fa;
                    width: fit-content;
                }
               

              



                .imagenesGrupo {
                    padding: 0px;
                    margin: 0px;
                    width: 0px;
                    height: 0px;
                }

                .imagenesGrupo img {
                    width: 0px;
                    height: 0px;
                }

                &:hover .imagenesGrupo {
                    position: relative;
                    top: -31px;
                    left: 145px;
                    width: 47px;
                    height: 20px;

                    display: flex;
                    align-items: center;
                    justify-content: center;
                    border-radius: 5px;
                    padding: 3px;
                }

                &:hover .imagenesGrupo img {
                    width: 20px;
                    height: 20px;
                    padding: 3px;
                    border-radius: 3px;
                    width: 20px;
                   
                }

                &:hover .imagenesGrupo img:hover {
                    background-color: rgba($color: rgb(240, 240, 240), $alpha: .95);
                    cursor: pointer;
                }
                .nombreEditarSubgrupo{
                    margin-left: 7px;
                    padding: 3px;
                    border-radius: 5px;
                    margin-top: 5px;
                    font-size: 15px;
                    max-width: 120px;
                }
            }
        }
    }

    .bodyAjustes>div {
        display: flex;
        gap: 20px;
        align-items: center;
    }

    .dias {
        padding-bottom: 20px;
        margin-bottom: 100px;

        &::-webkit-scrollbar {
            background-color: rgba(150, 166, 179, 0.1);
            height: 7px;
        }

        &::-webkit-scrollbar-thumb {
            background-color: rgb(182, 192, 201);
            padding: 200px;
        }

        &::-webkit-scrollbar-thumb:hover {
            background-color: rgb(6, 45, 79);
        }

        overflow: auto;

        &>div {
            display: flex;
            justify-content: center;
            align-items: stretch;
            padding: 0px 10px;
            gap: 15px;
            width: 2500px; //350+15 * 7
            margin-top: 20px;
            border-top: 1px solid rgb(190, 190, 190);

            .dia {

                width: 320px;
                min-height: 450px;
                padding: 20px 10px;
                border-radius: 3px;
                margin: 20px 0px;

                .headerDia {
                    display: flex;
                    justify-content: space-between;
                    padding: 5px;
                    font-weight: 400;
                    letter-spacing: 1px;


                }

                .headerDia>div {
                    display: flex;
                    align-items: center;
                    gap: 10px;
                }

            }

            .bodyDia,
            .headerDia {
                p {
                    padding: 2px 12px;
                    margin-bottom: 10px;
                    margin: 0px;

                }

                #contadorDia {
                    font-weight: 200;
                    font-size: 21px;
                    padding: 2px 0px;
                }
            }

            .bodyDia {


                .agregarTareas {

                    font-weight: 400;
                    padding: 10px 5px;
                    border-radius: 5px;
                    margin: 10px 0px 0px 0px;
                }

                .agregarTareas:hover {
                    background-color: rgba(203, 208, 211, 0.2);
                    cursor: pointer;
                }

                .agregarTareas p {
                    padding: 0px;
                }

            }

            .formularioTarea {
                position: relative;
                margin-bottom: 20px;
                position: relative;
                width: 100%;
                margin: 10px auto;

                box-shadow: 0px 0px 3px 0px rgba(108, 107, 107, 0.75);
                border-radius: 5px;

                form,
                .select,
                .subject {
                    border-radius: 5px;
                    background-color: white;
                }




                .select.subject {
                    box-shadow: 0px 0px 3px 0px rgba(108, 107, 107, 0.75);
                }



                .select p {
                    color: rgb(91, 91, 91);
                    font-size: 18px;
                    padding: 10px;
                    width: auto;
                }

                .select p:hover {
                    background-color: rgb(118, 137, 152);
                    color: white;
                    cursor: pointer;
                }

                .subjectOculto {
                    height: 0px;
                    width: 0px;
                    font-size: 0px;
                }

                .subjectOculto p {
                    height: 0px;
                    font-size: 0px;
                    padding: 0px;
                }

                .subject {
                    max-height: 250px;
                    position: absolute;
                    width: 100%;
                    overflow-y: auto;
                    z-index: 1;

                }

                .subject p {
                    height: auto;
                    font-size: 17px;
                }

                .subject p:hover {
                    background-color: rgb(146, 167, 185);
                    border-radius: 0px;
                    color: white;
                    cursor: pointer;
                }


                .gurpos {
                    max-height: 250px;
                    width: 100%;
                    overflow-y: auto;
                }

                .select:not(.subjectOculto) .grupos p {
                    font-size: 17px;
                    padding-left: 15px;
                }

                .grupos p:hover {
                    background-color: rgb(168, 190, 213);
                    color: white;
                    cursor: pointer;
                }

                .grupos p::before {
                    content: ': : ';
                }

                .gruposOcultos {
                    height: 0px;
                    width: 0px;
                }

                .gruposOcultos p {
                    font-size: 0px;
                    padding: 0px;
                }


                form input {
                    border: 0px;
                    background-color: transparent;
                    padding: 13px;

                    font-size: 18px;
                }

                form input:focus {
                    outline: none;
                    border: 0px solid white;
                }
            }


            .mostrarTarea {
                display: flex;
                flex-direction: column;


                .headerTarea {
                    position: relative;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;

                    background-color: white;
                    color: black;

                    padding: 15px 10px;
                    border-radius: 5px;
                    margin-bottom: 10px;
                    box-shadow: 0px 0px 3px 0px rgba(108, 107, 107, 0.75);

                    .tarea {
                        display: flex;
                        justify-content: center;
                        align-items: flex-start;
                        flex-direction: column;
                        width: 100%;
                        gap: 10px;
                        z-index: 0;

                        .tareaNombre {
                            width: 100%;
                            display: flex;
                            align-items: flex-start;
                            justify-content: space-between;

                            .tareaAjustes {
                                display: flex;
                                position: absolute;
                                left: 215px;
                                top: 7px;

                            }

                            .tareaAjustes img {
                                width: 0px;
                            }
                        }

                        .tareaNombre>p {
                            font-size: 18px;
                            font-weight: 600;
                            line-height: 1.3;
                        }

                        .tareaGrupo>p {
                            font-size: 16px;
                            font-weight: 300;
                            background-color: #dcc9fa;
                            padding: 3px 6px;
                            border-radius: 3px;
                        }
                    }

                    .tarea p {
                        padding: 0px;

                    }
                }

                .headerTarea:hover {
                    .tarea .tareaNombre .tareaAjustes img {
                        width: 20px;
                        z-index: 1;
                        padding: 5px 7px 5px 7px;
                    }

                    .tarea .tareaNombre .tareaAjustes {
                        background-color: rgb(255, 255, 255);
                        border-radius: 5px;

                    }

                    .tarea .tareaNombre .tareaAjustes img:hover {
                        background-color: rgba($color: rgb(240, 240, 240), $alpha: .95);


                        border-radius: 5px;
                        cursor: pointer;

                    }




                }



            }




        }

    }

    .tabla {
        overflow: hidden;

        &.dias>div {
            flex-wrap: wrap;
            width: auto;

            .dia {
                flex: 1 1 320px;
                max-width: 320px;
            }
        }
    }

}

